.app {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  background-color: #f0f0f0;
  transition: background-color 0.3s ease;

}

.dark-mode {
  background-color: black;
  color: #ffffff;
}
.dark-mode a{
  color: white;
}
.dark-mode .sidebar{
  background-color: black;
}
main {
  flex: 1;
  padding: 15px;
  margin-top: 13px;
  width: 100%;
}
#searchResult{
  display: grid;
  grid-template-columns: 1fr ;
  grid-gap: 10px;
  width: 100%;

  
}
#searchResult img{
  height: 350px;
  width: 100%;
  object-fit: cover;
  border-radius: 5px;
  border: 2px solid black;
}
a{
  text-decoration: none;
  color: black;
  width: 90%;
  font-size: 10px;
  font-family: sans-serif;
  font-weight: 900;
  text-align: center;
}
#loadMore{
  display: none;
  margin-left: 50%;
  font-size: 10px;
  color: yellow;
  background-color: black;
  cursor: pointer;
  height: 25px;
}
#loadMore:hover{
  color: white;
}
@media(min-width:760px){
  #searchResult{
    grid-template-columns: repeat(auto-fit,minmax(200px,1fr));
  }
  main{
    margin-top: 80px;
  }
  .sidebar{
    margin-top: 102px;
  }
}

@media(max-width:480px){
  main a{
    font-size: 9px;
  }
}
